.navbar-navbar {
  width: 100%;
  height: var(--dl-size-size-medium);
  display: flex;
  padding: 60px;
  z-index: 1000;
  position: relative;
  max-width: 100%;
  align-self: auto;
  align-items: center;
  flex-shrink: 1;
  background-color: var(--dl-color-primary-100);
}
.navbar-branding-logo {
  width: var(--dl-size-size-medium);
  height: var(--dl-size-size-medium);
  box-sizing: content-box;
}
.navbar-heading {
  color: var(--dl-color-primary-300);
  width: 120px;
  align-self: center;
  font-style: normal;
  font-family: Poppins;
  font-weight: 500;
  line-height: 3;
  letter-spacing: 10px;
  text-transform: uppercase;
}
.navbar-nav-content {
  gap: var(--dl-space-space-threeunits);
  color: var(--dl-color-primary-300);
  display: flex;
  align-items: center;
  margin-left: auto;
  flex-direction: row;
  justify-content: flex-start;
}
.navbar-nav-links {
  gap: var(--dl-space-space-threeunits);
  display: flex;
  align-items: flex-start;
  border-color: transparent;
}
.navbar-get-started {
  background-color: var(--dl-color-primary-button);
}
.navbar-text {
  color: var(--dl-color-gray-black);
  align-self: auto;
  font-style: Medium;
  text-align: left;
  font-family: Poppins;
  font-weight: 500;
  font-stretch: normal;
  text-decoration: none;
}
.navbar-hamburger {
  display: none;
}
.navbar-image {
  width: 100px;
  object-fit: cover;
}
.navbar-mobile-menu {
  gap: var(--dl-space-space-twounits);
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  display: flex;
  padding: var(--dl-space-space-twounits);
  z-index: 100;
  position: fixed;
  transform: translateX(100%);
  transition: 0.3s;
  align-items: stretch;
  flex-direction: column;
  background-color: var(--dl-color-primary-100);
}
.navbar-branding {
  display: flex;
  align-items: center;
  flex-direction: row;
  justify-content: space-between;
}
.navbar-image1 {
  width: 100px;
  filter: brightness(0) saturate(100%);
  object-fit: cover;
}
.navbar-container {
  display: flex;
  align-items: center;
  flex-direction: row;
  justify-content: space-between;
}
.navbar-icon {
  width: 24px;
  height: 24px;
}
.navbar-nav-links1 {
  gap: var(--dl-space-space-unit);
  color: var(--dl-color-gray-black);
  display: flex;
  flex-direction: column;
}
.navbar-text1 {
  color: rgba(255, 255, 255, 1);
  align-self: auto;
  font-style: Medium;
  text-align: left;
  font-family: Poppins;
  font-weight: 500;
  font-stretch: normal;
  text-decoration: none;
}
.navbar-root-class-name {
  background-color: transparent;
}
.navbar-root-class-name1 {
  background-color: transparent;
}
.navbar-root-class-name2 {
  background-color: transparent;
}

.navbar-root-class-name4 {
  background-color: transparent;
}
@media(max-width: 1200px) {
  .navbar-navbar {
    height: 50%;
  }
  .navbar-root-class-name {
    margin-top: var(--dl-space-space-fiveunits);
  }
}
@media(max-width: 991px) {
  .navbar-text {
    color: var(--dl-color-gray-black);
    font-family: Poppins;
    font-weight: 500;
    text-decoration: none;
  }
}
@media(max-width: 767px) {
  .navbar-navbar {
    padding-bottom: var(--dl-space-space-fourunits);
  }
  .navbar-branding-logo {
    width: 80px;
  }
  .navbar-nav-links {
    display: none;
  }
  .navbar-get-started {
    display: none;
  }
  .navbar-hamburger {
    display: flex;
    padding-left: var(--dl-space-space-unit);
    padding-right: var(--dl-space-space-unit);
  }
  .navbar-image {
    width: 16px;
    height: 14px;
  }
  .navbar-branding {
    width: 100%;
  }
  .navbar-nav-links1 {
    margin-top: var(--dl-space-space-twounits);
    align-items: flex-start;
    margin-bottom: var(--dl-space-space-twounits);
    justify-content: center;
  }
}
@media(max-width: 479px) {
  .navbar-heading {
    display: none;
  }
}
